<template>
    <div class="page_main">
        <el-form ref="form" class="form" :model="form" label-width="80px">
            <el-form-item label="姓名" class="left">
                <el-input v-model="form.name"  placeholder="请输入姓名"></el-input>
            </el-form-item>
            <el-form-item label="卡号"  class="left">
                <el-input v-model="form.card" placeholder="请输入卡号"></el-input>
            </el-form-item>
            <el-form-item  class="left">
                <el-button type="primary" plain>查询</el-button>
                <el-button type="primary" plain @click="highQuery()">高级查询</el-button>
                <el-button type="primary" plain>今日门诊</el-button>
            </el-form-item>
        </el-form>
        <div>
            <el-table :data="tableData" border :header-cell-style="{background:'#FAFAFA',height:'48px'}" size='' style="width: 100%">
                <el-table-column prop="userName" label="卡号" width="180"></el-table-column>  
                <el-table-column prop="personNo" label="姓名" width="80"></el-table-column>
                <el-table-column prop="scaleName" label="性别" width="80"></el-table-column>
                <el-table-column prop="userName" label="出生日期" width="">
                    <!-- <template slot-scope="subTime">
                                    {{subTime.row.stime.substring(0,10)}}
                                </template> -->
                </el-table-column>  
                <el-table-column prop="personNo" label="建档日期"></el-table-column>
                <el-table-column prop="scaleName" label="孕周"></el-table-column>
                <el-table-column prop="userName" label="出生体重" width=""></el-table-column>  
                <el-table-column prop="personNo" label="母亲姓名"></el-table-column>
                <el-table-column prop="scaleName" label="母亲电话"></el-table-column>
                <el-table-column prop="userName" label="父亲姓名" width=""></el-table-column>  
                <el-table-column prop="personNo" label="地址" width="180"></el-table-column>
                <el-table-column prop="scaleName" label="高危因素"></el-table-column>
            </el-table>
            <div class="page_bottom">
                <div class="block">
                    <el-pagination
                    background
                    :hide-on-single-page="value"
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage4"
                    :page-size="10"
                    layout="total, prev, pager, next, jumper"
                    :total="total">
                    </el-pagination>
                </div>
                <div class="left bottom_btn">
                    <el-button type="primary" plain  size="small" class="left" @click="exportFile()">导出档案</el-button>
                    <el-button type="primary" plain  size="small" class="left" @click="importFile()">导入档案</el-button>
                    <el-button type="primary" plain  size="small" class="left" @click="buildFile()">新建档案</el-button>
                </div>
            </div>
        </div>
        <el-dialog
            title="导出档案"
            :visible.sync="dialogExport"
            width="30%"
            >
            <span>这是一段信息</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogExport = false">取 消</el-button>
                <el-button type="primary" @click="dialogExport = false">立即下载</el-button>
            </span>
        </el-dialog>
        <el-dialog
            title="编辑档案"
            :visible.sync="dialogbuildFile"
            width="66%"
            >
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane label="幼儿信息" name="first">
                    <childInfo></childInfo>
                </el-tab-pane>
                <el-tab-pane label="母亲信息" name="second">
                    <motherInfo></motherInfo>
                </el-tab-pane>
                <el-tab-pane label="父亲信息" name="third">
                    <fatherInfo></fatherInfo>
                </el-tab-pane>
                <el-tab-pane label="高危因素" name="fourth">
                    <highriskInfo></highriskInfo>
                </el-tab-pane>
            </el-tabs>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogbuildFile = false">取 消</el-button>
                <el-button type="primary" @click="dialogbuildFile = false">立即保存</el-button>
            </span>
        </el-dialog>
         <el-dialog
            title="选择档案"
            :visible.sync="dialogQuery"
            width="50%"
            >
             <el-form ref="formBoard" class="formBoard" :model="formBoard"  label-width="80px">
                 <el-row>
                     <el-col :span='8'>
                         <el-form-item label="姓名"  class="left">
                            <el-input v-model="formBoard.name" placeholder="请输入姓名"></el-input>
                        </el-form-item>
                     </el-col>
                     <el-col  :span='8'>
                         <el-form-item label="性别"  class="left marginR">
                            <el-select v-model="formBoard.sex" clearable  placeholder="请选择性别">
                                    <el-option label="男" value="1"></el-option>
                                    <el-option label="女" value="2"></el-option>
                            </el-select>    
                        </el-form-item>
                     </el-col>
                     <el-col :span='8'>
                         <el-form-item label="母亲姓名"  class="left">
                            <el-input v-model="formBoard.motherName" placeholder="请输入姓名"></el-input>
                        </el-form-item>
                     </el-col>
                 </el-row>
                 <el-row>
                     <el-form-item label="出生日期"  class="left">
                            <el-date-picker
                                v-model="formBoard.birth"
                                style="width: 406px;"
                                type="daterange"
                                @change='changeBirth'
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期">
                        </el-date-picker>
                    </el-form-item>  
                 </el-row>
                <el-row>
                     <el-form-item label="建档日期"  class="left">
                            <el-date-picker
                                v-model="formBoard.buildFile"
                                style="width: 406px;"
                                type="daterange"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期">
                        </el-date-picker>
                    </el-form-item>  
                    <el-form-item  class="left">
                        <el-button type="primary" plain>查询</el-button>
                    </el-form-item>
                 </el-row>
                
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogQuery = false">取 消</el-button>
                <el-button type="primary" @click="dialogQuery = false">确定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import childInfo from './fileEditor/childInfo'
import motherInfo from './fileEditor/materInfo'
import fatherInfo from './fileEditor/fatherInfo'
import highriskInfo from './fileEditor/highriskInfo'
export default {
    components:{
      childInfo,
      motherInfo,
      fatherInfo,
      highriskInfo
    },
    data(){
       return {
           form: {
               name: '',
               card: ''
           },
           tableData: [
               {
                      userName: '1111',
                      personNo: '1111',
                      scaleName: '1111',
                      userName: '1111',
               }
           ],
            currentPage4: 1,
            total: 0,
            pageNumber: 1,
            pageSize: 10,
            value: false,
            dialogExport: false,
            dialogbuildFile:false,
            activeName: 'first',
            dialogQuery: false,
            formBoard: {
                name: '',
                sex: '',
                motherMame: '',
                birth: '',
                buildFile: '',
            },
           
       }
      
    },
    inject: ['reload'],
    
    created(){

    },
    methods: {
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
           
           
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            
        },
        exportFile(){
            this.dialogExport = true
        },
        importFile(){
            
        },
        buildFile(){
            this.dialogbuildFile = true
        },
        handleClick(tab, event) {
            console.log(tab, event);
        },
        highQuery(){
            this.dialogQuery = true
        },
        changeBirth(val){
            var start = new Date(val[0]);
            var end = new Date(val[1]);
            var birthStart = start.getFullYear() + '-' + (start.getMonth() + 1) + '-' + start.getDate()
            var birthEnd = end.getFullYear() + '-' + (end.getMonth() + 1) + '-' + end.getDate()
            console.log(birthStart,birthEnd)
        }
    },
}
</script>
<style lang="less" scoped>
.page_main{
    width: 100%;
    height: 100%;
    .form{
        height: 60px;
        margin-top: 20px;
    }
    .left{
        float: left;
    }
     .block{
        float: left;
        margin-top: 20px;
    }
    .bottom_btn{
        margin: 20px;
    }
    .page_bottom{
        height: 70px;
        position: absolute;
        bottom: 0;
    }
    .formBoard{
        height: 220px;
    }
}

</style>






