<template>
    <div class="page_main">
      <el-form ref="form" class="form" :model="form" label-width="80px">
            <el-row :gutter="20">
                <el-col :span="8">
                    <el-form-item label="幼儿姓名">
                        <el-input v-model="form.name"  placeholder="请输入幼儿姓名"></el-input>
                    </el-form-item>
                    <el-form-item label="出生体重">
                        <el-input v-model="form.weight"  placeholder="请输入出生体重">
                            <template slot="append">kg</template>
                        </el-input>
                    </el-form-item>
                    <el-form-item label="孕  周">
                        <el-input v-model="form.pregnantWeeks" style="width:40%;margin-right:6px;"  placeholder="请输入周"></el-input>周
                        <el-input v-model="form.pregnantDay" style="width:40%;margin-right:6px;" placeholder="请输入天"></el-input>天
                    </el-form-item>
                    <el-form-item label="胎次">
                        <el-input v-model="form.parity"  placeholder="请输入胎次"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="幼儿性别"  >
                        <!-- <el-input v-model="form.card" placeholder="请输入卡号"></el-input> -->
                        <el-select v-model="form.sex" placeholder="请选择幼儿性别">
                            <el-option label="男" value="男"></el-option>
                            <el-option label="女" value="女"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="出生身长">
                        <el-input v-model="form.bodyLength"  placeholder="请输入出生身长">
                            <template slot="append">cm</template>
                        </el-input>
                    </el-form-item>
                    <el-form-item label="分娩方式" >
                        <el-select v-model="form.delivery" placeholder="请选择分娩方式">
                            <el-option label="顺产" value="顺产"></el-option>
                            <el-option label="剖腹产" value="剖腹产"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="产次">
                        <el-input v-model="form.para"  placeholder="请输入产次"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="出生日期">
                        <!-- <el-input v-model="form.name"  placeholder="请输入姓名"></el-input> -->
                        <el-date-picker
                            v-model="form.birth"
                            type="date"
                            placeholder="选择日期">
                            </el-date-picker>
                    </el-form-item>
                    <el-form-item label="出生头围">
                        <el-input v-model="form.head"  placeholder="请输入出生头围">
                            <template slot="append">cm</template>
                        </el-input>
                    </el-form-item>
                    <el-form-item label="民族" >
                        <el-select v-model="form.national" placeholder="请选择民族">
                            <el-option v-for="item in nationalList"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="卡号">
                        <el-input v-model="form.cardNum"  placeholder="请输入卡号" :disabled="true"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="20">
                <el-col  :span="12">
                    <el-form-item label="籍贯">
                        <el-input v-model="form.native"  placeholder="请输入籍贯"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="居住地址">
                        <el-input v-model="form.address"  placeholder="请输入居住地址"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>
<script>

export default {
    data(){
       return {
           form: {
               name: '',
               sex: '',
               birth: '',
               weight: '',
               bodyLength: '',
               head: '',
               pregnantWeeks: '',
               pregnantDay: '',
               delivery: '',
               national: '',
               parity: '',
               para: '',
               cardNum: '111111111111',
               native: '',
               address: '',
           },
           nationalList: [
               {
                   value: '0',
                   label: '汉族'
               },
               {
                   value: '1',
                   label: '满族'
               },
           ]
       }
      
    },
    components:{
      
    },
    methods: {
            
    },
}
</script>
<style lang="less" scoped>
.page_main{
    width: 100%;
    height: 100%
}
</style>





