<template>
    <div class="page_main">
      <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
        <el-tab-pane label="胎儿高危因素" name="first">
            <el-radio-group v-model="radio" @change = 'changeRadio'  ref="radio" > 
                   <el-radio class="radio" :label="item.value"  v-for="item in childList" :key="item.value">{{item.lable}}</el-radio>
            </el-radio-group>
        </el-tab-pane>
        <el-tab-pane label="分娩期高危因素" name="second">配置管理</el-tab-pane>
        <el-tab-pane label="新生儿高危因素" name="third">角色管理</el-tab-pane>
    </el-tabs>
    </div>
</template>
<script>

export default {
    data(){
       return {
           activeName: 'first',
           radio: '',
           childList: [
               {
                   value: '1',
                   lable: '高龄产妇'
               },
               {
                   value: '2',
                   lable: '孕期疾病：高血压'
               },
               {
                   value: '3',
                   lable: '贫血'
               },
           ]
       }
      
    },
    components:{
      
    },
    methods: {
        handleClick(tab, event) {
            console.log(tab, event);
        },
        changeRadio(val){
            console.log(val)
        }
    },
}
</script>
<style lang="less" scoped>
.page_main{
    width: 100%;
    height: 310px;
    .radio{
        display: block;
        margin-bottom: 10px;
    }
}
</style>





